.assembleeGenerale {
  .wrapper--narrow {
    @extend .wrapper;
  }

  ul {
    list-style: none;
    margin-left: 0;
  }
  .accordion__link {
    background-color: $white;
    border: 1px solid $mediumGrey;
    margin-bottom: rem-calc(25*2/3);
    position: relative;
  }
  &__heading {
    display: table;
    padding-right: rem-calc(45);
    width: 100%;
  }
  &__list {
    margin-top: rem-calc(25*2/3);
  }
  &__title {
    display: table-cell;
    font-size: rem-calc(30*2/3);
    max-width: 70%;
    vertical-align: middle;
    &--sub {
      display: table-cell;
      font-size: rem-calc(20*2/3);
      text-align: right;
      vertical-align: middle;
    }
  }

  &__item {
    background-color: $lightGrey;
    margin-bottom: rem-calc(15*2/3);
    padding: rem-calc(25*2/3);

    &:last-child {
      margin-bottom: 0;
    }

    &__heading {
      font-family: $font-regular;
      color: $darkGrey;
      margin-bottom: rem-calc(15*2/3);
    }

    &__file {
      @include display(flex);
    }

    &__link {
      font-family: $font-regular;
      margin-right: rem-calc(30*2/3);
      &:last-of-type {
        margin-right: 0;
      }
      .icon {
        font-size: rem-calc(30);
        vertical-align: middle;
        color: $blue;
        margin-right: rem-calc(5);
      }

      > * {
        @include display(flex);
        @include align-items(center);
        span {
          color: $jet;
        }
      }
    }
  }
}

@media #{$medium-up} {
  .assembleeGenerale {
    .accordion__link {
      padding: rem-calc(25);
      .display-status {
        top: rem-calc(30);
        right: rem-calc(25);
      }
    }

    &__heading {
      padding: 0 rem-calc(70) 0 rem-calc(15);
    }
    &__title {
      font-size: rem-calc(30);
      &--sub {
        font-size: rem-calc(20);
      }
    }

    &__item {
      @include display(flex);
      @include align-items(center);
      padding: rem-calc(25);

      &__heading {
        @include flex(1);
        margin-bottom: 0;
      }

      &__file{
        margin-left: auto;
      }

      &__link {
        > *:hover {
          span {
            color: $blue;
          }
        }
      }
    }
  }
}
